import { Injectable } from '@angular/core';
import { Platform } from '@ionic/angular';
import { SecureStorageEcho,SecureStorageEchoObject } from '@ionic-native/secure-storage-echo/ngx';
import { AppData } from './appdata';



@Injectable({ providedIn: 'root' })
export class UUIDService {
    private storage: SecureStorageEchoObject;
  constructor(
    private secureStorage: SecureStorageEcho,
    private platform: Platform,
    private appdata : AppData
  ) {

  }

  
  async getOrCreateUUIDIos(): Promise<string> {
    await this.platform.ready();
    if (this.appdata.isIOS) {
    this.storage = await this.secureStorage.create('guest_storage');
    try {
      const storedUUID = await this.storage.get('guest_uuid');
      if (storedUUID) {
        return storedUUID;
      }
    } catch (e) {
      // No value found, proceed to generate a new one
    }

    const newUUID = this.generateUUID();
    await this.storage.set('guest_uuid', newUUID);
    return newUUID;
}else{
    const storedUUID = localStorage.getItem('guest_uuid');
    if (storedUUID) {
      return storedUUID;
    }

    const newUUID = this.generateUUID();
    localStorage.setItem('guest_uuid', newUUID);
    return newUUID;
}
  }

  private generateUUID(): string {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
      const r = Math.random() * 16 | 0;
      const v = c === 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }

}
